  --lb-color-warning-highest: var(--wp--preset--color--warning-800);
  --lb-color-warning-low: var(--wp--preset--color--warning-100);
  --lb-color-on-warning-container: var(--wp--preset--color--warning-600);
  --lb-color-warning-container: var(--wp--preset--color--warning-100);
  --lb-color-warning-container-low: var(--wp--preset--color--warning-50);
  --lb-color-warning-outline-variant-1: var(
    --wp--preset--color--warning-500
  );
  --lb-color-on-success: var(--wp--preset--color--white);
  --lb-color-success: var(--wp--preset--color--success-600);
  --lb-color-success-high: var(--wp--preset--color--success-700);
  --lb-color-success-highest: var(--wp--preset--color--success-800);
  --lb-color-success-low: var(--wp--preset--color--success-100);
  --lb-color-on-success-container: var(--wp--preset--color--success-600);
  --lb-color-success-container: var(--wp--preset--color--success-100);
  --lb-color-success-container-low: var(--wp--preset--color--success-50);
  --lb-color-success-outline-variant-1: var(
    --wp--preset--color--success-500
  );
  --lb-color-surface: var(--wp--preset--color--white);
  --lb-color-surface-highest: var(--wp--preset--color--gray-25);
  --lb-color-surface-high: var(--wp--preset--color--gray-50);
  --lb-color-surface-outline-1: var(--wp--preset--color--gray-200);
  --lb-color-surface-outline-2: var(--wp--preset--color--gray-300);
  --lb-color-on-surface-highest: var(--wp--preset--color--gray-900);
  --lb-color-on-surface-high: var(--wp--preset--color--gray-800);
  --lb-color-on-surface: var(--wp--preset--color--gray-700);
  --lb-color-on-surface-low: var(--wp--preset--color--gray-600);
  --lb-color-on-surface-lowest: var(--wp--preset--color--gray-500);
