  --lb-line-height-text-md-relative: var(--wp--custom--line-height--text-md);
  --lb-letter-spacing-text-md-relative: var(--wp--custom--letter-spacing--text-md);
  --lb-font-size-text-sm-relative: var(--wp--preset--font-size--text-sm);
