/**
* helpers
**/
/**
* font
**/
body {
  --lb--caption-color-text: var(--lb-color-text-default);
}

.wp-caption {
  max-width: 100%;
  padding: 1.45em 0 2.9em;
}

.alignleft {
  float: left;
  margin-inline-end: 2em;
  text-align: left;
}

.alignright {
  float: right;
  text-align: right;
  margin-inline-start: 2em;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.alignfull {
  margin-left: 0;
  margin-right: 0;
  width: auto;
  max-width: 100vw;
}

.alignwide {
  margin-left: 0;
  margin-right: 0;
  width: auto;
  max-width: 100vw;
}

.wp-caption.alignleft,
.wp-caption.alignright,
.wp-caption.aligncenter {
  padding-bottom: 2.9em;
}

.wp-caption p.wp-caption-text {
  padding: 0;
  margin-bottom: 0;
  font-style: italic;
  font-size: var(--lb-font-size-text-xs-relative);
  line-height: var(--lb-line-height-text-xs-relative);
  letter-spacing: var(--lb-letter-spacing-text-xs-relative, inherit);
}

.wp-caption.alignleft p.wp-caption-text {
  text-align: left;
}

.wp-caption.aligncenter p.wp-caption-text {
  text-align: center;
}

.wp-caption p.wp-caption-text a:hover {
  text-decoration: underline;
}

ul.wp-block-gallery.alignfull,
ul.wp-block-gallery.alignwide {
  margin-left: -8px !important;
  margin-right: -8px !important;
  width: auto !important;
  max-width: 100vw !important;
  margin-top: 1.45em !important;
  margin-bottom: 2.9em !important;
}

.alignwide img,
.alignfull img {
  display: block;
  margin: 0 auto;
}

.wp-block-image figcaption {
  padding: 0;
  margin-top: var(--wp--preset--spacing--55);
  margin-bottom: 0;
  text-align: center;
  color: var(--lb--caption-color-text);
  font-size: var(--lb-font-size-text-xs-relative);
  line-height: var(--lb-line-height-text-xs-relative);
  letter-spacing: var(--lb-letter-spacing-text-xs-relative, inherit);
}

.wp-block-image.aligncenter {
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 0;
}

.wp-block-image.aligncenter > figcaption,
.wp-block-image.alignleft > figcaption,
.wp-block-image.alignright > figcaption,
.wp-block-image.is-resized > figcaption {
  display: block;
}

.wp-block-gallery .blocks-gallery-image,
.wp-block-gallery .blocks-gallery-item {
  margin: 0;
}

.wp-block-cover.alignleft,
.wp-block-cover-image.alignleft {
  float: left;
  margin: 8px 30px 20px 0;
  padding: 0 30px;
}

.wp-block-cover.alignright,
.wp-block-cover-image.alignright {
  float: right;
  margin: 8px 0 20px 30px;
  padding: 0 30px;
}

/*themes*/
/**
* itertors
**/
body {
  font-family: var(--wp--preset--font-family--base);
  --lb-ui-color-text: var(--lb-color-text-default);
  --lb-ui-color-heading-text: var(--lb-color-text-strong);
  --lb-ui-color-link-text: var(--lb-color-action-text-default);
}

body :is(:where(h2):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-display-sm-relative);
  line-height: var(--lb-line-height-display-sm-relative);
  letter-spacing: var(--lb-letter-spacing-display-sm-relative, inherit);
  font-weight: 600;
}

body :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-display-xs-relative);
  line-height: var(--lb-line-height-display-xs-relative);
  letter-spacing: var(--lb-letter-spacing-display-xs-relative, inherit);
  font-weight: 600;
}

body :is(:where(p):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-text-md-relative);
  line-height: var(--lb-line-height-text-md-relative);
  letter-spacing: var(--lb-letter-spacing-text-md-relative, inherit);
}

body :is(:where(ul, ol):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-text-md-relative);
  line-height: var(--lb-line-height-text-md-relative);
  letter-spacing: var(--lb-letter-spacing-text-md-relative, inherit);
  list-style: disc;
  padding-left: var(--wp--preset--spacing--90);
  display: flex;
  flex-direction: column;
  row-gap: var(--wp--preset--spacing--80);
}

body :is(:where(ul):not(:where([class~=not-prose], [class~=not-prose] *))) {
  list-style: disc;
}

body :is(:where(ol):not(:where([class~=not-prose], [class~=not-prose] *))) {
  list-style: decimal;
}

body :is(:where(img):not(:where([class~=not-prose], [class~=not-prose] *))) {
  width: 100%;
}

body :is(:where(h1, h2, h3, h4, h5, h6, p, ul, ol, blockquote, .wp-block-image, .lb-pros-cons):not(:where([class~=not-prose], [class~=not-prose] *))):first-child {
  margin-block-start: 0;
}
body :is(:where(h1, h2, h3, h4, h5, h6, p, ul, ol, blockquote, .wp-block-image, .lb-pros-cons):not(:where([class~=not-prose], [class~=not-prose] *))):last-child {
  margin-block-end: 0;
}

body :is(:where(h2):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--160);
  margin-block-end: var(--wp--preset--spacing--100);
}

body :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--160);
  margin-block-end: var(--wp--preset--spacing--80);
}
@media (max-width: 767.98px) {
  body :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
    margin-block-start: var(--wp--preset--spacing--140);
  }
}

body :is(:where(p, ul, ol, .wp-block-image):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--80);
  margin-block-end: var(--wp--preset--spacing--80);
}

body :is(:where(blockquote):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--100);
  margin-block-end: var(--wp--preset--spacing--100);
}

body :is(:where(.lb-pros-cons):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--120);
  margin-block-end: var(--wp--preset--spacing--120);
}

body :is(:where(.lb-post-info):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--100);
  margin-block-end: var(--wp--preset--spacing--100);
}

body :is(:where(.lb-author-block):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin: var(--wp--preset--spacing--100) 0;
}
body :is(:where(.lb-author-block):not(:where([class~=not-prose], [class~=not-prose] *))):last-child {
  margin-bottom: 0;
}
body :is(:where(.lb-author-block):not(:where([class~=not-prose], [class~=not-prose] *))):first-child {
  margin-top: 0;
}

body :is(:where(h1, h2, h3, h4, h5, h6):not(:where([class~=not-prose], [class~=not-prose] *))) {
  color: var(--lb-ui-color-heading-text);
}

body :is(:where(a):not(:where([class~=not-prose], [class~=not-prose] *))) {
  color: var(--lb-ui-color-link-text);
}

body :is(:where(p, ul):not(:where([class~=not-prose], [class~=not-prose] *))) {
  color: var(--lb-ui-color-text);
}

