body {
  --lb-table-color-text: var(--lb-color-text-default);
  --lb-table-color-background: var(--lb-color-surface);
}

body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) {
  overflow: auto;
}
body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) table {
  background-color: var(--lb-table-color-background);
  border: 1px solid var(--wp--preset--color--gray-200);
  color: var(--lb-table-color-text);
  border-radius: var(--wp--preset--spacing--60);
  border-spacing: 0;
  border-collapse: separate !important;
}
body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) table thead {
  border: none;
}
body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) table tr:last-child td {
  border-bottom: 0;
}
body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) table td,
body :is(:where(.wp-block-table):not(:where([class~=not-prose], [class~=not-prose] *))) table th {
  padding: var(--wp--preset--spacing--80) var(--wp--preset--spacing--100);
  font-size: var(--lb-font-size-text-sm-relative);
  line-height: var(--lb-line-height-text-sm-relative);
